.sidebar {
  width: 100%;
  height: 3.5rem;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  background-color: var(--bg-color-100);
  border-top: 1px solid var(--white-color-200);
}
@media only screen and (min-width: 768px) {
  .sidebar {
    width: 5rem;
    height: 100%;
    border-right: 1px solid var(--white-color-200);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.07);
  }
}
.sidebar .main {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (min-width: 768px) {
  .sidebar .main {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.sidebar .main .sidebar-top {
  display: none;
}
@media only screen and (min-width: 768px) {
  .sidebar .main .sidebar-top {
    display: block;
    height: 20%;
    padding-top: 1rem;
  }
}
.sidebar .main .sidebar-center {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media only screen and (min-width: 768px) {
  .sidebar .main .sidebar-center {
    height: 60%;
  }
}
.sidebar .main .sidebar-center ul {
  gap: 0;
  padding-top: 0;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
@media only screen and (min-width: 768px) {
  .sidebar .main .sidebar-center ul {
    gap: 2.2rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.sidebar .main .sidebar-center ul .sidebar-btn {
  display: flex;
  width: 2.3rem;
  height: 2.3rem;
  border-radius: 20rem;
  cursor: pointer;
  padding: 0.6rem;
  background-color: transparent;
}
.sidebar .main .sidebar-center ul .sidebar-btn svg {
  width: 100%;
  height: 100%;
  fill: var(--black-color-300);
}
.sidebar .main .sidebar-center ul .sidebar-btn.active {
  background-color: var(--primary-color);
}
.sidebar .main .sidebar-center ul .sidebar-btn.active svg {
  fill: var(--white-color-100);
}
.sidebar .main .sidebar-bottom {
  display: none;
}
@media only screen and (min-width: 768px) {
  .sidebar .main .sidebar-bottom {
    height: 20%;
    display: flex;
    padding-bottom: 1rem;
    align-items: flex-end;
  }
}/*# sourceMappingURL=style.css.map */