@import "../../mixins.scss";
section#Contact{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    .page{
        width: 100%;
        height: 100%;
        max-width: 2400px;
        padding: 0 1.5rem;
        margin-bottom: 6rem;
        @include md{
            padding: 0 7rem;
            margin-bottom: 0;
        }
        @include xl{
            padding: 0 12rem;
        }
        @include xxl{
            padding: 0 15rem;
        }
        .header-div{
            .title{
                line-height: 2.8rem;
                font-size: 2.2rem;
            }
        }
        .social{
            @include flex-row-center;
            gap: 1rem;
            a{    
                width: 1.3rem;
                height: 1.3rem;
                padding: 0.1rem 0.25rem;
                border-radius: 100%;
                border: 0.1rem solid var(--black-color-400);
                svg{
                    width: 100%;
                    height: 100%;
                    fill: var(--black-color-400);
                }
                &:hover{
                    border-color: var(--primary-color);
                    svg{
                        fill: var(--primary-color);
                    }
                }
            }
        }
        .msg{
            color: var(--white-color-300);
            font-size: 1.8rem !important;
            font-weight: 600;
        }
        form{
            width: 100%;
            @include flex-col-center;
            padding-top: 2rem;
            .message{
                margin-top: 0.5rem;
                text-align: center;
                margin-bottom: 1rem;
                width: 100%;
                display: block;
                transition: var(--trans-4);
                color: var(--danger-color);
                font-size: 0.9rem;
                border: 1px solid var(--danger-color);
                padding: 0.5rem;
                &.success{
                    color: var(--success-color);
                    border-color: var(--success-color);
                }
                &:empty{
                    display: none;
                }
            }
            .inputBox{
                width: 95%;
                @include flex-col-center;
                margin-bottom: 1rem;
                @include md{
                    width: 30rem;
                }
                input, textarea{
                    width: 100%;
                    font-size: 1rem;
                    border-radius: var(--border-redius-100);
                    padding: var(--btn-padding-300);
                    background: var(--bg-color-200);

                }
                textarea{
                    width: 100%;
                    height: 5rem;
                    resize: none;
                    overflow: hidden;
                }
                button{
                    width: 40%;
                    border-radius: 20rem;
                    font-size: 1rem;
                    margin-top: 1rem;
                    color: var(--white-color-100);
                    font-weight: 600;
                    padding: var(--btn-padding-300);
                    background-color: var(--primary-color);
                    &:disabled{
                        opacity: 0.5;
                    }
                }
            }
        }
    }
    .center{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
