@import "../../mixins.scss";
.project-item{
    width: 95%;
    border-radius: 1rem;
    @include flex-col-center;
    align-items: stretch;
    justify-content: space-between;
    &.rev{
        flex-direction: column;
    }
    &.pro-1{
        background-color: #1e88e53d;
    }
    &.pro-2{
        background-color: #e539353d;
    }
    &.pro-3{
        background-color: #43a0473d;
    }
    &.pro-4{
        background-color: #fb8c003d;
    }
    @include sm{
        width: 75%;
    }
    @include md{
        width: 100%;
        flex-direction: row;
        &.rev{
            flex-direction: row-reverse;
        }
    }
    .project-left{
        width: 100%;
        height: 15rem;
        padding: 1.5rem 2rem;
        padding-bottom: 0;
        gap: 0.2rem;
        @include flex-col-center;
        align-items: start;
        @include md{
            height: 20rem;
            padding: 2.5rem;
        }
        .category{
            font-size: 0.7rem;
            color: var(--primary-color);
            font-weight: 700;
            text-transform: uppercase;
        }
        .title{
            line-height: 2rem;
            font-size: 1rem;
            @include ellipsis(1);
            @include md{
                font-size: 1.2rem;
            }
        }
        .description{
            font-size: 0.8rem !important;
            line-height: 1.1rem;
            text-align: left !important;
            margin-top: 0.2rem;
            color: var(--black-color-400);
            @include ellipsis(4);
            @include md{
                line-height: 1.6rem !important;
                font-size: 1rem !important;
                @include ellipsis(5);
            }
        }
        .view-btn{
            margin-top: 0.7rem;
            font-size: 0.8rem ;
            padding: var(--btn-padding-200);
            border-radius: var(--border-redius-400);
            background-color: var(--primary-color);
            color: var(--white-color-100);
            @include md{
                font-size: 1rem ;
            }
        }
    }
    .project-right{    
        height: 15rem;
        padding: 1rem;
        max-width: 100%;
        @include flex-row;
        justify-content: center;
        align-items: center;
        @include md{
            justify-content: flex-end;
            height: 20rem;
            padding: 2.5rem;
        }
        img{
            width: 100%;
            height: 100%;
            border-radius: 0.5rem;
            aspect-ratio: 6/4;
            @include md{
                width: 15rem;
                height: 75%;
            }
            @include lg{
                width: 25rem;
                height: 100%;
            }
        }
    }
}