@import "../../mixins.scss";
.stack-item{
    @include flex-col-center;
    width: 6rem;
    border-radius: 0.4rem;
    padding: 1rem;
    border: 0.1rem solid var(--bg-color-200);
    gap: 1rem;
    &:hover{
        transform: scale(1.05);
        transition: var(--trans-4);
        box-shadow: 0px 0px 10px 0px var(--bg-color-200); 
    }
    img{
        width: 2rem;
        height: 2rem;
    }
    .name{
        font-size: 0.7rem;
        line-height: 1.6rem;
        @include md{
            line-height: 1.8rem;
            font-size: 0.8rem;
        }
        @include xl{
            text-align: left;
        }
    }
}