section {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
section .page {
  width: 100%;
  height: 100%;
  max-width: 2400px;
  padding: 0 0.5rem;
}
@media only screen and (min-width: 768px) {
  section .page {
    padding: 0 7rem;
  }
}
@media only screen and (min-width: 1280px) {
  section .page {
    padding: 0 12rem;
  }
}
@media only screen and (min-width: 1536px) {
  section .page {
    padding: 0 15rem;
  }
}
section .page .sub-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;
}
section .page .sub-section .project {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 1rem;
}
section .center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}/*# sourceMappingURL=style.css.map */