@import "../../mixins.scss";
section{
    width: 100%;
    min-height: 100vh;
    height: 100%;
    @include flex-col-center;
    .page{
        width: 100%;
        height: 100%;
        max-width: 2400px;
        padding:0 0.5rem;
        @include md{
            padding: 0 7rem;
        }
        @include xl{
            padding: 0 12rem;
        }
        @include xxl{
            padding: 0 15rem;
        }
        .sub-section{
            width: 100%;
            @include flex-col-center;
            margin: 2rem 0;
            .project{
                @include flex-col-center;
                width: 100%;
                gap: 1rem;
            }
        }
    }
    .center{
        @include flex-col-center;
    }
}