@import "../../mixins.scss";
.sidebar{
    width: 100%;
    height: 3.5rem;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
    background-color: var(--bg-color-100);
    border-top: 1px solid var(--white-color-200);
    @include md{
        width: 5rem;
        height: 100%;
        border-right: 1px solid var(--white-color-200);
        box-shadow: 0 0 15px rgb(0 0 0 / 7%);
    }
    .main{
        height: 100%;
        @include flex-row-center;
        @include md{
            height: 100%;
            @include flex-col-center;
        }
        .sidebar-top{
            display: none;
            @include md{
                display: block;
                height: 20%;
                padding-top: 1rem;
            }
        }
        .sidebar-center{
            width: 100%;
            height: 100%;
            @include flex-col-center;
            @include md{
                height: 60%;
            }
            ul {
                gap: 0;
                padding-top: 0;
                width: 100%;
                @include flex-row;
                justify-content: space-evenly;
                @include md{
                    gap: 2.2rem;
                    height: 100%;
                    @include flex-col-center;
                }
                .sidebar-btn{
                    @include flex-row;
                    width: 2.3rem;
                    height: 2.3rem;
                    border-radius: 20rem;
                    cursor: pointer;
                    padding: 0.6rem;
                    background-color: transparent;
                    svg{
                        width: 100%;
                        height: 100%;
                        fill: var(--black-color-300);
                    }
                }
                .sidebar-btn.active{
                    background-color: var(--primary-color);
                        svg{
                            fill: var(--white-color-100);
                        }
                }
              }
        }
        .sidebar-bottom{
            display: none;
            @include md{
                height: 20%;
                @include flex-row;
                padding-bottom: 1rem;
                align-items: flex-end;
            }
        }
    }


}