@import "../../mixins.scss";
section{
    width: 100%;
    min-height: 100vh;
    height: 100%; 
    @include flex-col-center;
    .page{
        width: 100%;
        height: 100%;
        max-width: 2400px;
        padding:0 0.5rem;
        @include md{
            padding: 0 7rem;
        }
        @include xl{
            padding: 0 12rem;
        }
        @include xxl{
            padding: 0 15rem;
        }
        .sub-section{
            @include flex-row-center;
            margin: 2rem 0;
            .header-div{
                width: 100%;
                @include flex-col-center;
                margin: 1rem 0;
                .title{
                    text-align: center;
                    line-height: 2rem;
                    font-size: 1.6rem;
                    margin: 1rem 0;
                    @include md{
                        line-height: 2.8rem;
                        font-size: 2.2rem;
                    }
                    @include xl{
                        text-align: left;
                    }
                }
                .h-line{
                    background-color: var(--black-color-400);
                    width: 20%;
                    height: 2px;
                    margin: 0.8rem 0;
                    border-radius: 20rem;
                }
            }
            .description{
                width: 90%;
                text-align: center;
                font-size: 0.9rem;
                line-height: 1.6rem;
                @include md{
                    line-height: 1.8rem;
                    font-size: 1.2rem;
                }
            }
            .tab{
                min-width: 40%;
                max-width: 100%;
                @include flex-row;
                margin: 2rem 0;
                .tab-item{
                    padding: 1rem 2rem;
                    @include flex-row-center;
                    gap: 1rem;
                    width: 50%;
                    border: 0.1rem solid var(--bg-color-200);
                    &:first-child{
                        border-top-left-radius: 0.4rem;
                        border-bottom-left-radius: 0.4rem;
                    }
                    &:last-child{
                        border-top-right-radius: 0.4rem;
                        border-bottom-right-radius: 0.4rem;
                    }
                    .number{
                        color: var(--primary-color);
                        font-size: 1.6rem;
                        @include md{
                            font-size: 2.2rem;
                        }
                    }
                    .description{
                        font-size: 0.7rem;
                        line-height: 1.2rem;
                        color: var(--black-color-400);
                        @include md{
                            line-height: 1.5rem;
                            font-size: 1rem;
                        }
                    }
                }
            }
            .stack{
                @include flex-row-center;
                gap: 2rem;
                width: 100%;
                flex-wrap: wrap;
                @include sm{
                    width: 90%;
                }
            }
        }
    }
    .center{
        @include flex-col-center;
    }
}