section#Contact {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
}
section#Contact .page {
  width: 100%;
  height: 100%;
  max-width: 2400px;
  padding: 0 1.5rem;
  margin-bottom: 6rem;
}
@media only screen and (min-width: 768px) {
  section#Contact .page {
    padding: 0 7rem;
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 1280px) {
  section#Contact .page {
    padding: 0 12rem;
  }
}
@media only screen and (min-width: 1536px) {
  section#Contact .page {
    padding: 0 15rem;
  }
}
section#Contact .page .header-div .title {
  line-height: 2.8rem;
  font-size: 2.2rem;
}
section#Contact .page .social {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
section#Contact .page .social a {
  width: 1.3rem;
  height: 1.3rem;
  padding: 0.1rem 0.25rem;
  border-radius: 100%;
  border: 0.1rem solid var(--black-color-400);
}
section#Contact .page .social a svg {
  width: 100%;
  height: 100%;
  fill: var(--black-color-400);
}
section#Contact .page .social a:hover {
  border-color: var(--primary-color);
}
section#Contact .page .social a:hover svg {
  fill: var(--primary-color);
}
section#Contact .page .msg {
  color: var(--white-color-300);
  font-size: 1.8rem !important;
  font-weight: 600;
}
section#Contact .page form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 2rem;
}
section#Contact .page form .message {
  margin-top: 0.5rem;
  text-align: center;
  margin-bottom: 1rem;
  width: 100%;
  display: block;
  transition: var(--trans-4);
  color: var(--danger-color);
  font-size: 0.9rem;
  border: 1px solid var(--danger-color);
  padding: 0.5rem;
}
section#Contact .page form .message.success {
  color: var(--success-color);
  border-color: var(--success-color);
}
section#Contact .page form .message:empty {
  display: none;
}
section#Contact .page form .inputBox {
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}
@media only screen and (min-width: 768px) {
  section#Contact .page form .inputBox {
    width: 30rem;
  }
}
section#Contact .page form .inputBox input, section#Contact .page form .inputBox textarea {
  width: 100%;
  font-size: 1rem;
  border-radius: var(--border-redius-100);
  padding: var(--btn-padding-300);
  background: var(--bg-color-200);
}
section#Contact .page form .inputBox textarea {
  width: 100%;
  height: 5rem;
  resize: none;
  overflow: hidden;
}
section#Contact .page form .inputBox button {
  width: 40%;
  border-radius: 20rem;
  font-size: 1rem;
  margin-top: 1rem;
  color: var(--white-color-100);
  font-weight: 600;
  padding: var(--btn-padding-300);
  background-color: var(--primary-color);
}
section#Contact .page form .inputBox button:disabled {
  opacity: 0.5;
}
section#Contact .center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}/*# sourceMappingURL=style.css.map */