section {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
section .page {
  width: 100%;
  height: 100%;
  max-width: 2400px;
  padding: 0 0.5rem;
}
@media only screen and (min-width: 768px) {
  section .page {
    padding: 0 7rem;
  }
}
@media only screen and (min-width: 1280px) {
  section .page {
    padding: 0 12rem;
  }
}
@media only screen and (min-width: 1536px) {
  section .page {
    padding: 0 15rem;
  }
}
section .page .sub-section {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;
}
section .page .sub-section .header-div {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem 0;
}
section .page .sub-section .header-div .title {
  text-align: center;
  line-height: 2rem;
  font-size: 1.6rem;
  margin: 1rem 0;
}
@media only screen and (min-width: 768px) {
  section .page .sub-section .header-div .title {
    line-height: 2.8rem;
    font-size: 2.2rem;
  }
}
@media only screen and (min-width: 1280px) {
  section .page .sub-section .header-div .title {
    text-align: left;
  }
}
section .page .sub-section .header-div .h-line {
  background-color: var(--black-color-400);
  width: 20%;
  height: 2px;
  margin: 0.8rem 0;
  border-radius: 20rem;
}
section .page .sub-section .description {
  width: 90%;
  text-align: center;
  font-size: 0.9rem;
  line-height: 1.6rem;
}
@media only screen and (min-width: 768px) {
  section .page .sub-section .description {
    line-height: 1.8rem;
    font-size: 1.2rem;
  }
}
section .page .sub-section .tab {
  min-width: 40%;
  max-width: 100%;
  display: flex;
  margin: 2rem 0;
}
section .page .sub-section .tab .tab-item {
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  width: 50%;
  border: 0.1rem solid var(--bg-color-200);
}
section .page .sub-section .tab .tab-item:first-child {
  border-top-left-radius: 0.4rem;
  border-bottom-left-radius: 0.4rem;
}
section .page .sub-section .tab .tab-item:last-child {
  border-top-right-radius: 0.4rem;
  border-bottom-right-radius: 0.4rem;
}
section .page .sub-section .tab .tab-item .number {
  color: var(--primary-color);
  font-size: 1.6rem;
}
@media only screen and (min-width: 768px) {
  section .page .sub-section .tab .tab-item .number {
    font-size: 2.2rem;
  }
}
section .page .sub-section .tab .tab-item .description {
  font-size: 0.7rem;
  line-height: 1.2rem;
  color: var(--black-color-400);
}
@media only screen and (min-width: 768px) {
  section .page .sub-section .tab .tab-item .description {
    line-height: 1.5rem;
    font-size: 1rem;
  }
}
section .page .sub-section .stack {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: 100%;
  flex-wrap: wrap;
}
@media only screen and (min-width: 640px) {
  section .page .sub-section .stack {
    width: 90%;
  }
}
section .center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}/*# sourceMappingURL=style.css.map */