.stack-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 6rem;
  border-radius: 0.4rem;
  padding: 1rem;
  border: 0.1rem solid var(--bg-color-200);
  gap: 1rem;
}
.stack-item:hover {
  transform: scale(1.05);
  transition: var(--trans-4);
  box-shadow: 0px 0px 10px 0px var(--bg-color-200);
}
.stack-item img {
  width: 2rem;
  height: 2rem;
}
.stack-item .name {
  font-size: 0.7rem;
  line-height: 1.6rem;
}
@media only screen and (min-width: 768px) {
  .stack-item .name {
    line-height: 1.8rem;
    font-size: 0.8rem;
  }
}
@media only screen and (min-width: 1280px) {
  .stack-item .name {
    text-align: left;
  }
}/*# sourceMappingURL=style.css.map */