.project-item {
  width: 95%;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: stretch;
  justify-content: space-between;
}
.project-item.rev {
  flex-direction: column;
}
.project-item.pro-1 {
  background-color: rgba(30, 136, 229, 0.2392156863);
}
.project-item.pro-2 {
  background-color: rgba(229, 57, 53, 0.2392156863);
}
.project-item.pro-3 {
  background-color: rgba(67, 160, 71, 0.2392156863);
}
.project-item.pro-4 {
  background-color: rgba(251, 140, 0, 0.2392156863);
}
@media only screen and (min-width: 640px) {
  .project-item {
    width: 75%;
  }
}
@media only screen and (min-width: 768px) {
  .project-item {
    width: 100%;
    flex-direction: row;
  }
  .project-item.rev {
    flex-direction: row-reverse;
  }
}
.project-item .project-left {
  width: 100%;
  height: 15rem;
  padding: 1.5rem 2rem;
  padding-bottom: 0;
  gap: 0.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: start;
}
@media only screen and (min-width: 768px) {
  .project-item .project-left {
    height: 20rem;
    padding: 2.5rem;
  }
}
.project-item .project-left .category {
  font-size: 0.7rem;
  color: var(--primary-color);
  font-weight: 700;
  text-transform: uppercase;
}
.project-item .project-left .title {
  line-height: 2rem;
  font-size: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media only screen and (min-width: 768px) {
  .project-item .project-left .title {
    font-size: 1.2rem;
  }
}
.project-item .project-left .description {
  font-size: 0.8rem !important;
  line-height: 1.1rem;
  text-align: left !important;
  margin-top: 0.2rem;
  color: var(--black-color-400);
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media only screen and (min-width: 768px) {
  .project-item .project-left .description {
    line-height: 1.6rem !important;
    font-size: 1rem !important;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.project-item .project-left .view-btn {
  margin-top: 0.7rem;
  font-size: 0.8rem;
  padding: var(--btn-padding-200);
  border-radius: var(--border-redius-400);
  background-color: var(--primary-color);
  color: var(--white-color-100);
}
@media only screen and (min-width: 768px) {
  .project-item .project-left .view-btn {
    font-size: 1rem;
  }
}
.project-item .project-right {
  height: 15rem;
  padding: 1rem;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  .project-item .project-right {
    justify-content: flex-end;
    height: 20rem;
    padding: 2.5rem;
  }
}
.project-item .project-right img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  aspect-ratio: 6/4;
}
@media only screen and (min-width: 768px) {
  .project-item .project-right img {
    width: 15rem;
    height: 75%;
  }
}
@media only screen and (min-width: 1024px) {
  .project-item .project-right img {
    width: 25rem;
    height: 100%;
  }
}/*# sourceMappingURL=style.css.map */